import React from 'react'

import './Navbar.css'
import Toggle from './Toggle'
const Navbar = () => {
  return (
    <>
    <nav className="navbar navbar-expand-lg">
    <div className="container-fluid">
      <a className="navbar-brand" href="/">WA2</a>
      <Toggle/>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav ms-auto mb-2 mb-lg-0 text-center">
        
          <li className="nav-item">
            <a className="nav-link ms-3" aria-current="page" href="#Home">Home</a>
          </li>
          <li className="nav-item">
          <a className="nav-link ms-3" aria-current="page" href="#Services">Services</a>
        </li>
        <li className="nav-item">
        <a className="nav-link ms-3" aria-current="page" href="#Experience">Experience</a>
      </li>
      <li className="nav-item">
      <a className="nav-link ms-3" aria-current="page" href="#Portfolio">Portfolio</a>
    </li>
    <li className="nav-item">
    <a className="nav-link ms-3" aria-current="page" href="#Testimonials">Testimonials</a>
  </li>
        </ul>
        <div>
    <button className="button n-button ms-5">Contact Us</button>
    </div>
      </div>
    </div>

  </nav>

 

</>
  )
}

export default Navbar